import { useEffect, useState } from "react";
import axios from "axios";
const App = () => {
  const [tools, setTools] = useState([]);
  useEffect(() => {
    axios.get("/api/tools").then((res) => {
      setTools(res.data);
    });
  }, []);
  console.log(tools);
  return (
    <div>
      {tools.map((t) => (
        <div>
          {t.name}
          <img src={t.imageUrls} />
        </div>
      ))}
    </div>
  );
};

export default App;
